<template>
  <fragment v-if="row">
    <tr>
      <template v-if="row.division_code">
        <td class="nowrap-table">{{ row.division_name }}</td>
        <td class="text-uppercase text-center nowrap-table">{{ row.division_code }}</td>
        <td class="text-uppercase text-center nowrap-table">{{ row.division_category }}</td>
      </template>
      <template v-else>
        <td :colspan="row.series_months || row.data_series ? row.division_code? 0 : 3 : 15" :class="[row.has_background? 'bg-light-info font-weight-bolder': '', row.series_months || row.data_series ? '': 'font-weight-bold', row.title? 'bg-muted': 'text-left']"> {{ row.title || row.item_name }} </td>
      </template>
      <template v-if="row.series_months">
        <td v-for="(c,i) in row.series_months" :key="i+'-series_months'" class="nowrap-table min-w-100px">
          <div v-if="c.value" class="d-flex align-items-center justify-content-between px-0">
            <span class="pr-3">Rp.</span>
            <span v-if="c.value < 0">({{ c.value | parse('number') }})</span>
            <span class="pr-2" v-else>{{ c.value | parse('number') }}</span>
          </div>
        </td>
      </template>
      <template v-if="row.data_series">
        <td v-for="(c,i) in row.data_series" :key="i+'-data_series'" class="nowrap-table min-w-100px">
          <div v-if="c.value" class="d-flex align-items-center justify-content-between px-0">
            <span class="pr-3">Rp.</span>
            <span v-if="c.value < 0">({{ c.value | parse('number') }})</span>
            <span class="pr-2" v-else>{{ c.value | parse('number') }}</span>
          </div>
        </td>
      </template>
    </tr>
    <capex-node v-for="(x,y) in row.datas" :row="x" :key="y+'-loop-data'" />
  </fragment>
</template>

<script>
import { Fragment } from 'vue-fragment'
export default {
  name: 'capex-node',
  props: ['row'],
  components: { Fragment }
}
</script>

<style>
</style>



